import React from "react";
import { useHistory } from "react-router-dom";
import { useDispatch,useSelector } from "react-redux";
import Header from "../Header";
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline";
import axios from "axios";
import Footer from "../footer";
import {Link} from 'react-router-dom'

import Qs from "querystring"

const SentMessagedetails = (props) => {

  const [errors, setErrors] = React.useState([]);
  const currentUser = useSelector(({ user }) => user.currentUser);

  const [loading, setLoading] = React.useState(false);
  const history = useHistory();
  const dispatch = useDispatch();
  const state = history.location.state
 const search= Qs.parse(history.location.search)
 const token = currentUser && currentUser.token;
  const [formResponse, setFormResponse] = React.useState({
    firstName: "",
    message: "",
    lastName: "",
    company: "",
    mobileNumber: "",
    subject: "",
    email: "",
  });

  const validatePage = async () => {
    const {
  
    
      message,
     
    } = formResponse;
    const newError = new Array();

    if (!message) {
      newError.push("Message fields can not be blank");
    }


    return newError;
  };
  const handleNext = async (e) => {
    e.preventDefault();
    await validatePage().then(async (res) => {
      if (res.length > 0) {
        setErrors(res);
        window.scrollTo(0, 30);
      } else {
        const Data={body:formResponse.message,receiver:search.posters_id,title:search.title}
        setLoading(true);
        await axios
          .post(`/api/v1/sendMessages`, Data,{headers:{ Authorization:token}})
          .then((response) => {
            setLoading(false);
            history.replace("/Contactsuccess");
          })
          .catch((err) => {
            setLoading(false);

            if (err.response && err.response.data.message) {
              setErrors([err.response.data.message]);
            } else {
              setErrors([
                "An error occured, make sure you have a working network",
              ]);
            }
            console.log(err);
          });
        // history.push("/Contactsuccess");
      }
    });
  };
  return (
    <>
      <Header />
      <div className="topPatch" style={{ height: "120px" }} />
      <section className="booking-form bg-gray ">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">


          <div style={{minHeight:"450px"}} className="bg-white px-30 py-40 shadow w-75 mx-auto">
                <div >
                  <h3 className="color-secondary line-bottom pb-15 mb-20 wow slideInDown animated">
               

                  Re: {state.title}
                  </h3>


                  <h6 className="color-secondary pb-1 mb-20 wow slideInDown animated">
                  to: {state.receiver.firstName}
                  </h6>
                  <div className="row justify-content-center">
                    <div className="col-lg-7 text-left">
                      {errors.length > 0
                        ? errors.map((xxx) => (
                            <p className="error-card alert-danger">{xxx}</p>
                          ))
                        : null}
                      <div style={{ height: "10px" }} />
                    </div>
                  </div>

                 
                  <div style={{minHeight:"200px"}} className='col-lg-10 mb-20'>
                    <p>
                   {state.body}
                    </p>
                  </div>
                  <div  className="row">
              
                
               
                    <div className="col-lg-12">
             
                
                    </div>
                    
                  </div>
                </div>
              </div>
          




             
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default SentMessagedetails;
